import React, { Component } from "react";
import './educationalGroup.scss';
import Delete from '../../assets/images/forms/delete.svg';
import edit from '../../assets/images/forms/edit.svg';
import plus from '../../assets/images/forms/plus.svg'
import dummyData from '../../assets/images/dummy.svg'

class Honors extends Component {

    constructor(props) {
        super(props)
        this.state = {
            data: [{ name: '' }]
        }
    }
    addItem() {
        let data = this.state.data;
        data.push({ name: '' })
        this.setState({ data: data })
    }

    delete(item) {
        let data = this.state.data;
        data.splice(item, 1)
        this.setState({ data: data })
    }
    render() {
        let self = this;
        var listItems = this.state.data.map(function (element, idx) {
            return (

                <div key={idx} className={'col-md-4'}>
                    <div className={'row'}>
                        <div className={'list-box text-right'}>
                            <img src={Delete} alt={'delete img'} onClick={() => self.delete(idx)} />


                            <ul>
                                <li>
                                    <div className="input-group" dir={'ltr'}>
                                        <div className="input-group-prepend">
                                            <span className="input-group-text">
                                                <img src={edit} alt={'edit img'} />
                                            </span>
                                        </div>
                                        <input type="text" className="form-control text-right" placeholder=" عنوان جشنواره یا مسابقه" />
                                    </div>
                                </li>

                                <li>
                                    <img src={dummyData} alt={'dummy data'} className={'images'} />
                                </li>
                                <li>

                                    <div className="input-group" dir={'ltr'}>
                                        <div className="input-group-prepend">
                                            <span className="input-group-text">
                                                <img src={edit} alt={'edit img'} />
                                            </span>
                                        </div>
                                        <input type="text" className="form-control text-right" placeholder=" رتبه یا مقام" />
                                    </div>
                                </li>

                                <li>
                                    <div className="input-group" dir={'ltr'}>
                                        <div className="input-group-prepend">
                                            <span className="input-group-text">
                                                <img src={edit} alt={'edit img'} />
                                            </span>
                                        </div>
                                        <input type="text" className="form-control text-right" placeholder=" سال برگزاری" />
                                    </div>
                                </li>

                            </ul>
                        </div>
                    </div>
                </div>

            )
        })
        return (
            <div className={'box-card'}>
                <div className={'box-card-header'}>
                    <div className={'box-card-header-title'}>
                        افتخارات
                    </div>

                    <div className={'box-card-header-description'}>
                        در این قسمت می توانید افتخارات
                        خود را ثبت کنید
                    </div>
                </div>

                <div className={'box-card-content'}>
                    <div className={'col-md-12'}>
                        <div className={'row'}>
                            {listItems}

                            <div className={'col-md-4'}>
                                <div onClick={() => this.addItem()} className={'row'}>
                                    <div className={'list-box-plus big-plus-box'}>
                                        <img src={plus} alt={'plus img'} />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Honors
