import React, { Component } from "react";
import './educationalGroup.scss';
import Delete from '../../assets/images/forms/delete.svg';
import plus from '../../assets/images/forms/plus.svg'

class Description extends Component {

    constructor(props) {
        super(props)
        this.state = {
            data: [{ name: '' }]
        }
    }
    addItem() {
        let data = this.state.data;
        data.push({ name: '' })
        this.setState({ data: data })
    }

    delete(item) {
        let data = this.state.data;
        data.splice(item, 1)
        this.setState({ data: data })
    }

    render() {
        let self = this;
        var listItems = this.state.data.map(function (element, idx) {
            return (

                <div key={idx} className={'col-md-12'}>
                    <div className={'row'}>
                        <div className={'big-box text-right'}>
                            <img src={Delete} alt={'delete img'} onClick={() => self.delete(idx)} />
                            <input className={'form-control'} placeholder={'عنوان توضیحات'} />

                            <textarea rows={'5'} className={'form-control'} placeholder={'متن توضیحات'} />
                        </div>
                    </div>
                </div>

            )
        })
        return (
            <div className={'box-card'}>
                <div className={'box-card-header'}>
                    <div className={'box-card-header-title'}>
                        توضیحات
                    </div>

                    <div className={'box-card-header-description'}>
                        در این قسمت می توانید برای معرفی بیشتر خودبه همراه عکس توضیحات اضافه کنید
                    </div>
                </div>

                <div className={'box-card-content'}>
                    <div className={'col-md-12'}>
                        <div className={'row'}>
                            {listItems}
                            <div className={'col-md-12'}>
                                <div onClick={() => this.addItem()} className={'row'}>
                                    <div className={'big-plus'}>
                                        <img src={plus} alt={'plus img'} />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Description
